import React, { Fragment, useState } from 'react'
import { Text } from '../Text'
import css from './Image.module.css'

/**
 * Usuario: bb-frontend-6
 * Descripción: Crea una imagen
 * param { url,alt,tittle,typeImg,dataStyle,addClass }
 * - url: ruta de la imagen a mostrar.
 * - alt: nombre alternativo de la imagen
 * - title: hace referencia si la misma es una imagen o animacion
 * - typeImg: extension de la imagen por defecto es .png
 * - dataStyle: estilo del marco de la imagen, contene default y del 1 al 4
 * - addClass: para insertar clases adicionales
 **/
export const Image = ({
  url = 'prueba3',
  alt = 'Imagen de prueba.',
  title = 'Imagen 1.',
  typeImg,
  dataStyle = '2',
  addClass,
  width = 350
}) => {
  const [error, setError] = useState(false)
  //funcion de error, al no conseguir la imagen cambia el estado
  const onError = (value) => {
    setError(value)
  }

  //if ternario donde al no conseguir la imagen enviada muestra una por default
  let imgToSee = !error
    ? `${url}${typeImg}`
    : `data:image/png;base64,`

  return (
    <Fragment>
      <figure className={addClass}>
        <div
          className={css.containerImg}
          data-style={dataStyle}
          style={{ maxWidth: `${width}px` }}
        >
          <img
            src={imgToSee}
            onError={onError}
            alt={alt}
            data-style-img={dataStyle}
          />
        </div>
        <figcaption>
          <div className={css.containerText}>
            <Text
              type='p'
              children={
                <Fragment>
                  <b>{title}</b> <span>{alt}</span>
                </Fragment>
              }
            />
          </div>
        </figcaption>
      </figure>
    </Fragment>
  )
}

Image.defaultProps = {
  typeImg: '.png'
}
